<template>
  <div id="app" element-loading-text="欢迎使用"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)" v-loading.fullscreen.lock="loading">
     <router-view />
  </div>
</template>

<script>
import fast from '@/utils/store'
export default {
  data () {
    return {
      user: null, // 登录用户信息
      loading:false,
    }
  },
  mounted () {
    this.user = JSON.parse(localStorage.getItem('user'))
    this.loading = true;
    // 获取最新版本
    this.fetchEdition()
    setTimeout(()=>{
      this.loading = false;
    },1000)
  },
  methods: {
    // 获取最新版本
    fetchEdition () {
      let requestOptions = {
        method: 'GET',
        time: 60000,
        redirect: 'follow'
      }
      fetch(fast.appversion, requestOptions)
        .then(response => response.text())
        .then(res => {
          this.$store.commit('changeNewEdition', JSON.parse(res).Data)
        })
        .catch(error => {
          return error
        })
    }
  }
}
</script>

