<template>
  <div class="mainPage">
    <el-container style="height: 100%; border: 1px solid #eee">
      <el-aside width="245px">
        <el-menu
          style="background: #f2f2f2; border-right: none"
          class="el-menu-vertical-demo"
          :router="true"
          :collapse="isCollapse"
          v-if="navList && navList.length"
        >
          <div class="user">
            <i class="iconfont icon-yonghu tou"></i>

            <div class="changeLang">
              <div class="info">
                <p class="name" v-if="user">{{ user.username }}</p>
                <span class="iwrap">
                  <el-popover
                    v-if="userStr"
                    placement="right"
                    popper-class="popover"
                    width="150px"
                    trigger="click"
                  >
                    <div class="qrcode" ref="qrCodeUrl"></div>
                    <p style="text-align: center">{{ $t("Home_mobile") }}</p>
                    <i class="el-icon-full-screen" slot="reference"></i>
                  </el-popover>
                  &nbsp;

                  <router-link
                    tag="i"
                    class="iconfont icon-yonghushezhi"
                    to="/MyInfo"
                    style="cursor: pointer"
                  ></router-link>
                  &nbsp;

                  <i
                    class="iconfont icon-tuichu"
                    style="cursor: pointer"
                    @click="exit"
                  ></i>
                  &nbsp;
                </span>
              </div>
            </div>
          </div>
          <div v-for="item in navList" :key="item.id" class="nav">
            <router-link tag="div" :to="item.link" class="navItem">
              <i :class="item.icon"></i>
              <span>{{ $t(item.key) }}</span>
            </router-link>
          </div>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header
          style="
            text-align: right;
            font-size: 12px;
            background-color: #067fb5;
            border-radius: 10px 0px 0px 10px;
            margin-bottom: 35px;
          "
          height="80px"
        >
          <h1 class="logo">
            <p>基于Docker容器网络系统</p>
          </h1>
        </el-header>
        <el-main style="padding: 0px 20px 0px 0px">
          <div class="main">
            <transition :name="transitionName" mode="out-in">
              <router-view class="child-view"></router-view>
            </transition>
          </div>
        </el-main>
      </el-container>
    </el-container>
    <myFooter />
  </div>
</template>

<script>
import Vue from "vue";
import {
  Container,
  Menu,
  Slider,
  Aside,
  Header,
  Main,
  Popover,
  select,
  option,
} from "element-ui";
import QRCode from "qrcodejs2";
import navList from "@/router/navList";
// import http from '@/utils/http'
Vue.use(Container);
Vue.use(Menu);
Vue.use(Slider);
Vue.use(Aside);
Vue.use(Header);
Vue.use(Main);
Vue.use(Popover);
Vue.use(option);
Vue.use(select);

export default {
  data() {
    return {
      isCollapse: false,
      user: null, // 登录用户信息
      userStr: null,
      lang: "简体中文",
      qrcode: "",
      transitionName: "slide-right",
      navList: navList,
    };
  },
  computed: {
    options() {
      if (this.$i18n && this.$i18n.messages) {
        return Object.keys(this.$i18n.messages);
      } else {
        return [];
      }
    },
  },
  mounted() {
    this.lang = localStorage.getItem("Language") || this.lang;
    this.user = JSON.parse(localStorage.getItem("user"));
    if (
      this.user.baseURL.indexOf("127.0.0.1") !== -1 ||
      this.user.baseURL.indexOf("localhost") !== -1
    ) {
      const baseURL =
        location.protocol +
        "//" +
        window.returnCitySN.cip +
        ":" +
        location.port;
      this.userStr = `username=${this.user.username}%26token=${this.user.token}%26baseURL=${baseURL}`;
    } else {
      this.userStr = `username=${this.user.username}%26token=${this.user.token}%26baseURL=${this.user.baseURL}`;
    }
    // 获取当前选中的菜单
    this.fetchData();
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    $route: "fetchData",
  },
  beforeRouteUpdate(to, from, next) {
    if (to && from) {
      const toDepth = to.path;
      const fromDepth = from.path;
      let trs = this.getId(toDepth) < this.getId(fromDepth);
      this.transitionName = trs ? "slide-right" : "slide-left";
    }
    next();
  },
  methods: {
    ChangeLanguage() {
      this.$i18n.locale = this.lang;
      document.title = this.$t("web_title");
      localStorage.setItem("Language", this.lang);
    },
    creatQrCode() {
      if (!this.user) {
        return;
      }
      if (this.qrcode && this.$refs.qrCodeUrl)
        this.$refs.qrCodeUrl.innerHTML = ""; // 清除代码
      this.$nextTick().then(() => {
        this.qrcode = new QRCode(this.$refs.qrCodeUrl, {
          text: `${this.user.baseURL}?${this.userStr}`, // 需要转换为二维码的内容
          width: 150,
          height: 150,
          colorDark: "#000000",
          colorLight: "#ffffff",
          correctLevel: QRCode.CorrectLevel.H,
        });
      });
    },
    // 退出登录，清空cookie，localStorage
    exit() {
      //localStorage.removeItem("baseURL");
      localStorage.removeItem("user");
      localStorage.removeItem("token");
      localStorage.removeItem("userList");
      this.$router.replace("/Login");
    },
    // 获取当前路由判断当前的菜单
    getId(link) {
      let id = 0;
      let navList = this.navList;
      for (let i = 0; i < navList.length; i++) {
        if (navList[i].link === link) {
          id = i;
          return id;
        }
      }
      return id;
    },
    fetchData() {
      this.creatQrCode();
    },
  },
};
</script>

