import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isFetchContainer: false, // 是否需要重新拉取容器列表数据
    isUpload: false, // 是否需要上传数据
    crateData: {}, // 8个子组件的数据汇总到这里
    changeCrateDataNum: 0, // 记录changeCrateData被调用的次数
    edition: 'v21.7.8', // 当前版本
    newEdition: '', // 最新版本
    code:1
  },
  mutations: {
    changeIsFetchContainer (obj, data) { // 修改isFetchContainer
      this.state.isFetchContainer = data
    },
    changeIsUpload (obj, data) { // 修改isUpload
      this.state.isUpload = data
      // console.log(this.state.isUpload)
    },
    // 添加数据
    changeCrateData (obj, data) {
      if (data) { // 子组件修改crateData，并记录次函数调用次数
        this.state.crateData[data[0]] = data[1]
        this.state.changeCrateDataNum++
        // console.log('添加数据', this.state.crateData, this.state.changeCrateDataNum)
      } else { // data没有值时，表示重置数据
        this.state.crateData = {}
        this.state.changeCrateDataNum = 0
        // console.log('重置', this.state.crateData, this.state.changeCrateDataNum)
      }
    },
    // 修改newedion的值
    changeNewEdition (obj, data) {
      this.state.newEdition = data
    }
  },
  actions: {
  },
  modules: {
  }
})
